import { makeAutoObservable, } from "mobx";
import { showError, showLoading, showSuccess } from "../components/Message";
import { CreatorNewsItem, CreatorNewsService } from "../services/CreatorService";

class CreatorStore {
  public loading: boolean = false;
  public loadingMore: boolean = false;
  public newsList: CreatorNewsItem[] = [];
  public detail?:CreatorNewsItem;
  private _page: number = 1;
  private noMore: boolean = false;


  constructor() {
    makeAutoObservable(this);
  }

  public async getData(loadmore?: boolean) {

    if (this.loading || this.loadingMore) {
      return;
    }
    let response: Array<CreatorNewsItem>;

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }

    try {
      response = await new CreatorNewsService(this._page).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore=false;
      this.noMore =true;
      // showError(e);
      return;
    }
    this.loading = false;
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.length === 0;
      this.newsList.push(...response);
    } else {
      this.newsList = response;
    }
  }
}

const creatorStore = new CreatorStore();

export { creatorStore };