import AliyunUpload from "aliyun-upload-vod";
import { useCallback, useEffect, useRef, useState } from "react";
import { VodAuth, VodService } from "../services/VodService";
import { EventBus } from './eventBus';

export class Uploader extends EventBus {

  id: any = new Date().getTime().toString() + Math.random();

  ossUploader: any;

  uploadAuth: any;

  uploadAddress: any;

  status: any = 'init';

  url: any;

  videoId: any;

  get fileName(): any {
    return this.file.name ?? '';
  }

  constructor(private file: any) {
    super();
    let videoId = '';
    this.ossUploader = new AliyunUpload.Vod({
      timeout: 60000,
      partSize: 1048576,
      parallel: 5,
      retryCount: 3,
      retryDuration: 2,
      region: "cn-shenzhen",
      userId: "5725013695904696",
      // 
      addFileSuccess: function (uploadInfo: any) {
        console.log("", uploadInfo);
        console.log("props.checkpoint", uploadInfo.checkpoint);
        // setSize(uploadInfo.checkpoint.fileSize);
      },
      // 
      onUploadstarted: async (uploadInfo: any) => {
        console.log("onUploadstarted", uploadInfo.checkpoint);
        var auth: VodAuth;
        auth = await new VodService(".mp4").send();
        const uploadAuth = this.uploadAuth = auth.uploadAuth;
        const uploadAddress =this.uploadAddress = auth.uploadAddress;
        videoId = auth.videoId;
        this.ossUploader.setUploadAuthAndAddress(
          uploadInfo,
          uploadAuth,
          uploadAddress,
          videoId
        );
        this.changeStatus('uploading')
      },
      // 
      onUploadSucceed: (uploadInfo: any) => {
        // setTips("");
        console.log("uploadInfo sucesss", uploadInfo);
        this.url = `https://video.olicreater.com/${uploadInfo.object}`;
        this.videoId = videoId;
        this.changeStatus('success');
        this.emit('success', uploadInfo);
        // onComplete(, videoId);
        // onComplete(`https://${uploadInfo.bucket}.oss-cn-shenzhen.aliyuncs.com/${uploadInfo.object}`);
      },
      // 
      onUploadFailed: (uploadInfo: any, code: any, message: any) => {
        // setTips("");
        this.changeStatus('error');
        this.emit('error', uploadInfo, code, message);
      },
      // 
      onUploadCanceled: function (uploadInfo: any, code: any, message: any) {},
      // , 
      onUploadProgress: (
        uploadInfo: any,
        totalSize: any,
        progress: any
      ) => {
        this.emit('progress', this, uploadInfo, totalSize, progress);
        // console.log("onUploadProgress:file:" + uploadInfo.file.name + ", fileSize:" + totalSize + ", percent:" + Math.ceil(progress * 100) + "%")
        // let progressPercent = Math.ceil(progress * 100);
        // setpRrogress(progressPercent);
      },
      // 
      onUploadTokenExpired: function (uploadInfo: any) {},
    });
  }

  changeStatus(status: string) {
    this.status = status;
    this.emit('statusChange', this, status);
  }

  upload() {
    this.ossUploader.addFile(this.file, null, null, null, '{"Vod":{}}');
    this.ossUploader.startUpload();
  }

  pause() {
    this.changeStatus('pause');
    this.ossUploader.stopUpload();
  }

  clear() {
    this.events = {};
    this.ossUploader.stopUpload();
  }
}

export function useUploaderList() {
  const listRef = useRef([] as Uploader[]);
  const listStatusRef = useRef(false);
  const [list, setList] = useState([] as Uploader[]);
  // 
  const uploadNext = useCallback((uploader: Uploader, status) => {
    if (status === 'success' || status === 'error') {
      const i = listRef.current.indexOf(uploader);
      if (i > -1) {
        const theNext = listRef.current.find(it => it.status === 'init');
        if (theNext) {
          theNext.upload();
        } else {
          listStatusRef.current = false;
        }
      }
    }
  }, []);
  const startUpload = useCallback((file: any) => {
    const uploader = new Uploader(file);
    uploader.addListener('statusChange', uploadNext);
    if (!listStatusRef.current) {
      uploader.upload();
      listStatusRef.current = true;
    }
    setList((oList) => listRef.current = [...oList, uploader]);
  }, []);
  const remove = useCallback((uploader: Uploader) => {
    uploader.clear();
    setList((oList) => listRef.current = oList.filter(it => it !== uploader));
  }, []);

  useEffect(() => {
    return () => listRef.current.forEach(it => it.clear());
  }, []);

  return { list, startUpload, remove };
}

export function useUploader(uploader: Uploader) {
  const [status, setStatus] = useState(uploader.status);
  useEffect(() => {
    const change = (_: any, status: string) => setStatus(status);
    uploader.addListener('statusChange', change);
    return () => uploader.removeListener('statusChange', change);
  }, [uploader]);


  return {
    status,
    pause: () => uploader.pause(),
    resume: () => uploader.upload()
  };
}

export function useUploaderProgress(uploader: Uploader) {
  const [progress, setProgress] = useState({ total: 0, percent: 0 });
  useEffect(() => {
    const cb = (_1: any, _2: any, total: number, percent: number) => {
      setProgress({ total: total, percent });
    };
    uploader.addListener('progress', cb);
    return () => uploader.removeListener('progress', cb);
  }, [uploader]);
  return progress;
}