import "./Videos.scss";
import { FC, useEffect, useMemo, useState } from "react";
import "react-awesome-slider/dist/styles.css";
import classNames from "classnames";
import { CommentPage } from "../Creator/Comment";
import { translate } from "../../components/Text";
import { RouteComponentProps, useHistory } from "react-router";
import { useRequest, useCurrentUser, useScrollToBottomEvent } from "../../utils/hooks";
import {
  CreatorCollectionDetailService,
  CreatorVideoItem,
  CreatorVideoService,
} from "../../services/CreatorVideoService";
import { VideoLikeService, VideoService } from "../../services/VideoService";
import { CreatorSubscribeListService, CreatorSubscribeService, CreatorUserService } from "../../services/CreatorService";
import { observer } from "mobx-react";
import { userStore } from "../../stores/UserStore";
import './Videos.scss';
import 'react-awesome-slider/dist/styles.css';
import { Avatar, Image, Img } from "../../components/Image";
import { StripePayArea } from "../Stripe/Stripe";
import { CollectionDataService } from "../../services/CollectionListService";
import { LocalStorageService } from "../../utils/storage";
import GlobalConfig from "../../config/GlobalConfig";
import IconCry from '../../images/creator/cry.png';
import { VideoPlayLoader } from "../Skelleton/videoPlay";
import { VideoReportModal } from "./VideoReport";
import { verifyRule } from "../../utils/globalFuncs";
import { Ajax } from "../../utils/http";
import { LoginModel, loginStore } from "../../stores/LoginStore";
import IconLike from '../../images/main/like.png';
import IconLikeOn from '../../images/main/likeOn.png';
import IconReport from '../../images/main/report.png';
import { Switch } from "antd";

function itemTrans(it: any) {
  return { ...it, lookauth: it.islookauth };
}

function getPlayVideo(id: string): Promise<any> {
  return new VideoService(1, 2, { id }).send();
}

function getVideoList({ type = "c", cid = "" }): Promise<any> {
  if (type === "c") {
    return Promise.all([
      new CollectionDataService(cid).send(),
      new CreatorCollectionDetailService(cid).send()
    ]).then(([collInfo, list]) => {
      if (!collInfo) {
        return Promise.reject(new Error(''));
      }

      return {
        auth: userStore.userInfo?.id === collInfo.userid ? '1' : collInfo.lookauth,
        isfree: collInfo.isfree,
        trycount: collInfo.trycount,
        info: { name: `${collInfo.name}`, price: collInfo.price },
        list: list.map((it) => ({ ...it, price: collInfo.price, collectid: collInfo.id }))
      };
    });
  } else {

    let resPromise: Promise<CreatorVideoItem[]>;
    if (type === "l") {
      resPromise = new VideoService(1, 8, { labelid: cid })
        .send()
        .then((res) => res.map(itemTrans));
    } else if (type === 'up') {
      resPromise = new CreatorVideoService({ isfree: 1, psize: 999, userid: cid }).send();
    } else if (type === 'uf') {
      resPromise = new CreatorVideoService({ isfree: 0, psize: 999, userid: cid }).send();
    } else if (type === 'ua') {
      resPromise = new CreatorVideoService({ psize: 999, userid: cid }).send();
    } else if (type === 'hot') {
      resPromise = new CreatorVideoService({ ishot: 1, psize: 8 }).send();
    } else {
      resPromise = new VideoService(1, 6)
        .send()
        .then((res) => res.map(itemTrans));
    }
    return resPromise.then(list => ({
      auth: 1,
      isfree: 0,
      trycount: 0,
      list, info: { name: translate('home.video.e') }
    }));
  }
}

function sendVideoLike({ id = "", islike = 0 }) {
  return new VideoLikeService(id, islike).send();
}

const getUserData = ({ id = "" }) => new CreatorUserService(id).send();

const getAllFollowIds = () => new CreatorSubscribeListService({ userid: userStore.userInfo?.id, psize: 999 }).send().then(res => res.map(it => it.id));

const doFollow = ({ userid = '', status = '' }) => new CreatorSubscribeService({ objectid: userid, objecttype: 1, status }).send();

const sendHistory = ({ id = '' }) => new Ajax('/user/userHistory', { objectid: id, objecttype: 5 }).post();

const AuthPage = observer(({ userid = "" }) => {
  const his = useHistory();
  const { data: user, loading, load } = useRequest(getUserData, { autoLoad: false });
  const { data: allFollowIdsData, loading: foloading } = useRequest(getAllFollowIds);

  const [allFollowIds, setAllFollowIds] = useState([] as string[]);
  const [hasFollowOnInit, setHasFollowOnInit] = useState(false);
  useEffect(() => load({ id: userid }), [userid]);
  useEffect(() => {
    setAllFollowIds(allFollowIdsData ?? []);
    setHasFollowOnInit((allFollowIdsData ?? []).indexOf(userid) > -1);
  }, [allFollowIdsData]);

  const hasFollow = (allFollowIds ?? []).indexOf(userid) > -1;
  const isFollowCountDiff = hasFollow !== hasFollowOnInit;
  const hasLogin = !!userStore.userInfo;

  const doSub = () => {
    if (!hasLogin) {
      loginStore.LoginModel = LoginModel.login;
      userStore.loginModalVisible = true;
    } else if (hasFollow) {
      setAllFollowIds(allFollowIds.filter(i => i !== userid));
      doFollow({ userid, status: '0' });
    } else {
      setAllFollowIds(allFollowIds.concat([userid]));
      doFollow({ userid, status: '1' });
    }
  };

  if (!user || loading || foloading) {
    return <div />
  }

  return (
    <div className="user-info">
      <Avatar
        width={50}
        height={50}
        src={user.avatar}
        onClick={() => his.push(`/main/userCenter/${userid}/videoList`)}
      />
      <div className="right-info">
        <div className="user-tx-info">
          <div>
            <div className="author">{user.username}</div>
            <div className="desc">
              {Math.max(0, parseInt(user.fanscount ?? '0', 10) + (!isFollowCountDiff ? 0 : (hasFollow ? 1 : -1)))} {translate("creator.myCenter.2")}
            </div>
          </div>
          {userid !== userStore.userInfo?.id && (
            <button className={classNames('sub-btn', { cancel: hasFollow })} onClick={doSub}>
              {translate(`creator.author.${hasFollow ? '2' : '1'}`)}
            </button>
          )}
        </div>
        <div className="profile">{user.profile}</div>
      </div>
    </div>
  );
});

export const VideosPlayPage: FC<
  RouteComponentProps<{ cid: string; id: string }>
> = observer((props) => {
  let { cid, id } = props.match.params;
  const his = useHistory();
  const user = useCurrentUser();

  // const { data: video, load:loadVideo, loading:loadingVideo } = useRequest(getPlayVideo, { autoLoad: false });
  const { data: rawData, load: loadList, loading: loadingList } = useRequest(getVideoList, { autoLoad: false });
  const { loadWithPromise: doLike } = useRequest(sendVideoLike, {
    autoLoad: false,
  });

  const data = useMemo(() => (rawData?.list ?? []).map((it: any) => ({ ...it, lookauth: user?.id === it.userid ? 1 : it.lookauth })), [rawData, user?.id]);
  const [video, setVideo] = useState<any>(undefined);
  const [islike, setIslike] = useState(0);
  const [needBuy, setNeedBuy] = useState(false);
  const [hasBought, setBought] = useState(false);
  const [buyTips, setBuyTips] = useState(false);
  const [defaultTty, setDefaultTry] = useState(false);
  const [isfree, setIsfree] = useState(false);
  const [tryMinute, setTryMinute] = useState('0');
  const [reportVisible, setReportVisible] = useState(false);
  const isCollMode = /^c(\d+)$/.test(cid);

  useEffect(() => {
    let isUa = cid.indexOf("ua") > -1;
    if (video && video.id == id) {
      return;
    }
    let delay = 0;
    if (video) {
      delay = 500;
      setVideo(undefined);
    }
    setTimeout(() => {
      if (id !== '0' && !isCollMode && !rawData) {
        setBought(false);
        getPlayVideo(id).then((v) => {
          let _video = v.find((item: any) => item.type == cid || (isUa && item.type == 'videocreator'));
          setVideo(_video)
        });
      } else if (rawData) {
        if (id == '0') {
          setVideo(rawData.list[0]);
        } else {
          let _video = rawData.list.find((l: any) => l.id == id);
          if (_video) {
            setVideo(_video);
          }
        }
      }
    }, delay)


  }, [id, rawData]);

  const reload = () => {
    const [_, type, tid] = /^(c|l|up|uf|ua)(\d+)$/.exec(cid) ?? [];
    if (type) {
      loadList({ type, cid: tid });
    } else {
      loadList({ type: "hot" });
    }
  };

  const checkNeedBuy = (currentTime: number) => {
    let isLogin = LocalStorageService.get("_userInfo");
    let tryminute;
    let isMember = verifyRule('researchReport', undefined, true);
    let _isfree = isCollMode ? rawData.isfree == '1' : video.isfree == '1';
    if (isLogin) {
      if (_isfree) {
        tryminute = isMember ? parseInt(video?.tryminute ?? '0', 10) : 1;
        setDefaultTry(!isMember);
      } else {
        tryminute = parseInt(video?.tryminute ?? '0', 10);
      }
    } else {
      tryminute = 1;
      setDefaultTry(true);
    }
    setTryMinute(`${tryminute}`);
    let hasAuth = (video?.islookauth == 1 || video?.lookauth == 1) && isLogin;
    let isVip = _isfree && isMember;
    let vipBought = _isfree && parseFloat(video.price || '0') > 0;
    // 
    if (hasAuth && ((isCollMode && (isVip || !_isfree)) || (isVip || vipBought || !_isfree))) {
      setBuyTips(false);
      setNeedBuy(false);
    }
    // 
    else if (tryminute > 0) {
      setBuyTips(true);
      setNeedBuy(currentTime > tryminute * 60);
    }
    // 
    else {
      setBuyTips(false);
      setNeedBuy(true);
    }
  }

  useEffect(reload, [cid]);

  useEffect(() => {
    if (video) {
      let isLogin = LocalStorageService.get("_userInfo");
      let _isfree = isCollMode ? rawData.isfree == '1' : video.isfree == '1';
      setIsfree(_isfree);
      setIslike(video.islike);
      if (isLogin) {
        if (_isfree && verifyRule('researchReport', undefined, true) || (isCollMode && rawData.auth == '1') || video.userid == user?.id) {
          video.lookauth = 1;
        }
      }

      sendHistory({ id: video.id });
      checkNeedBuy(0);

    }
  }, [video]);
  let isLogin = LocalStorageService.get("_userInfo");
  return (
    <div className="videoPlayPage">
      <div className="videoWrapper">
        {(!video || loadingList) ?
          <VideoPlayLoader />
          : <>
            <div className="leftContent">
              <div className="playContent">
                {buyTips && !needBuy && !hasBought && <div className="buyTips"><span> {translate('payment.9', { value: tryMinute })}</span>
                </div>}
                {needBuy && !hasBought ? (
                  <div className="video-pay">
                    {isLogin && <h1>{translate(`creator.myVideos.${parseFloat(video.price) > 0 ? (isfree ? 53 : 47) : 54}`)}</h1>}
                    {!isLogin && <h1>{translate(`creator.myVideos.55`)}</h1>}
                    <img src={IconCry} />
                    <div className="pay-area">
                      {isLogin && parseFloat(video.price) > 0 && <StripePayArea
                        disableMask={true}
                        targetInfo={{ title: isCollMode ? (rawData?.info.name || '') : video?.title, avatar: video?.avatar, subTitle: video?.username }}
                        objectid={isCollMode ? video.collectid : video.id}
                        ordertype={isCollMode ? 4 : 3}
                        price={video.price}
                        onComplete={() => {
                          setBought(true);
                        }}
                      >
                        <span className="price">${video?.price}</span>
                      </StripePayArea>}
                      {isfree && isLogin && <span className="price vip" onClick={(e) => {
                        GlobalConfig.history.push('/subscribe');
                      }}>{translate('mine.subscribe.j')}</span>}
                      {!isLogin && <span className="price login" onClick={(e) => {
                        loginStore.LoginModel = LoginModel.login;
                        userStore.loginModalVisible = true;
                      }}>{translate('mine.login.h')}</span>}
                    </div>
                  </div>
                ) : (
                  <video
                    onTimeUpdate={(evt) => {
                      let duration: number = evt.currentTarget.duration;
                      let currentTime: number = evt.currentTarget.currentTime;
                      checkNeedBuy(currentTime)
                      if (currentTime >= duration && LocalStorageService.get("autoPlay")) {
                        let videoIdx = 0;
                        for (var i = 0; i < data.length; i++) {
                          if (data[i].id == video.id && i != data.length - 1) {
                            videoIdx = i + 1;
                            break;
                          }
                        }
                        his.push(data[videoIdx].id);
                      }
                    }}
                    controlsList="nodownload"
                    src={video.url}
                    // src='http://aoliapp.oss-cn-shenzhen.aliyuncs.com/assets%2Fvideo%2Fjinx.mp4'
                    controls={true}
                    autoPlay={true}
                    muted={true}
                  ></video>
                )}
              </div>
              <div className="info">
                <div className="text">
                  <div className="title">{video.title}</div>
                  <div className="view">
                    {video.viewcount} {translate("home.video.a")} {video.createtime.slice(0,16)}
                  </div>
                </div>
                <div
                  className="like"
                  onClick={() => {
                    let isLogin = LocalStorageService.get("_userInfo");
                    if (!isLogin) {
                      loginStore.LoginModel = LoginModel.login;
                      userStore.loginModalVisible = true;
                      return;
                    }
                    setIslike(islike ? 0 : 1);
                    let likeCount: number = parseInt(video.likecount);
                    video.likecount = islike ? likeCount - 1 : likeCount + 1;
                    doLike({ id: video.id, islike: islike ? 0 : 1 });
                  }}
                >
                  <Img
                    className="left"
                    src={IconLikeOn}
                    alt=""
                  />
                  <div className="likeCount left">{video.likecount}</div>
                </div>
                {user?.id != video.userid && <div className="report" onClick={() => setReportVisible(true)}>
                  <Img
                    className="left"
                    src={IconReport}
                    alt=""
                  /> <span>{translate('report.3')}</span>
                </div>}
              </div>
              <AuthPage userid={video.userid} />
              {video && <CommentPage isSelf={video.userid == userStore.userInfo?.id} objectid={video.id} objecttype={video.objecttye} />}
            </div>
            <div className="rightContent">
              <div className="top">
                <h1>{rawData?.info.name}</h1>
                <div className="switch">
                  <span>{translate('home.video.l')}</span>
                  <Switch defaultChecked={LocalStorageService.get("autoPlay")} onChange={(v) => {
                    if (v) {
                      LocalStorageService.set("autoPlay", '1');
                    } else {
                      LocalStorageService.remove("autoPlay")
                    }
                  }} />
                </div>

              </div>
              {data.map((l: any, i: number) => (
                <StripePayArea
                  key={i}
                  disableMask={true}
                  targetInfo={{ title: rawData?.info.name || '', avatar: l.avatar, subTitle: l.username }}
                  objectid={isCollMode ? l.collectid : l.id}
                  ordertype={isCollMode ? 4 : 3}
                  price={'0'}
                  onClick={() => {
                    his.push(l.id);
                  }}
                  onComplete={() => {
                    reload();
                    his.push(l.id);
                  }}
                  className={classNames("item", { active: video.id === l.id })}
                >
                  <div className="video-thumb-wrapper">
                    <Image className="video-thumb" src={l.thumb} alt="" />
                    {isCollMode && l.isfree != '1' ?
                      (parseInt(rawData.trycount || '0') > i && <div className="free">{translate('creator.myVideos.10')}</div>) :
                      (parseFloat(l.price) > 0 && <div className="need-price">${l.price}</div>)
                    }
                    {l.isfree == '1' && <div className="need-price">VIP</div>}
                  </div>
                  <div className="info">
                    <div className="title">{l.title}</div>
                    <div className="view">
                      {l.viewcount} {translate("home.video.a")}
                    </div>
                    <div className="date">{l.createtime.slice(0,16)}</div>
                  </div>
                </StripePayArea>
              ))}
            </div>
          </>}
      </div>
      {video && <VideoReportModal videoid={video.id} visible={reportVisible} cancle={() => setReportVisible(false)} />}
    </div>
  );
});
