import { FC, useEffect, useState } from "react";
import { Button, formatSize, Modal } from "./Common";
import { IconAdd, IconAdd2, IconDelete, IconPause, IconUpload, IconUploading, IconUploadSuccess, IconVideoFiles, IconVideoFormat, IconVideoScreen } from "./Icons";
import { message, Upload, Select, Radio } from 'antd';
import classNames from 'classnames';
import { useUploaderList, Uploader, useUploader, useUploaderProgress } from "../../utils/upload";
import { ImageUploader } from "../../components/ImageUploader";
import { useRequest, useTranslator } from "../../utils/hooks";
import { PublishVideoService } from "../../services/PublishService";
import { showError, showSuccess } from "../../components/Message";
import { translate } from "../../components/Text";
import { LabelItem } from "../../services/VideoLabelSerice";
import NumericInput from "../../components/NumericInput";
import { Ajax } from "../../utils/http";
import { mainStore } from "../../stores/MainStore";

const { Dragger } = Upload;



function formatFileName(fileName: string) {
  const m = /^(.+)\.([\w]+)$/.exec(fileName);
  if (m) {
    return `${m[1].slice(0, 7)}...${m[2]}`;
  } else {
    return `${fileName.slice(0, 10)}...`;
  }
}

const VideoUploadingItem: FC<{ className?: string; uploader: Uploader; onCancel: () => void; onSelect: () => void }> = ({ className, uploader, onCancel, onSelect }) => {
  const { status, pause, resume } = useUploader(uploader);
  const { total, percent } = useUploaderProgress(uploader);
  const { translate } = useTranslator();
  const percentTxt = `${Math.ceil(percent * 100)}%`;
  let statusCentent;
  if (status === 'init') {
    statusCentent = (
      <>
        {IconUploading}
        <div className="info">{translate('creator.myVideos.26')}</div>
      </>
    );
  } else if (status === 'uploading') {
    statusCentent = (
      <>
        {IconUploading}
        <div className="info">{translate('creator.myVideos.27')}{percentTxt}</div>
      </>
    );
  } else if (status === 'success') {
    statusCentent = (
      <>
        {IconUploadSuccess}
        <div className="info">{translate('creator.myVideos.28')}</div>
      </>
    );
  }
  return (
    <div className={classNames('upload-list-item', className)}>
      <div className="up-area" onClick={onSelect}>
        <div className="status">
          {translate('creator.myVideos.29')}
        </div>
        {statusCentent}
      </div>
      <div className="down-area">
        <div className="file-info">
          <span className="file-name">{formatFileName(uploader.fileName)}</span>
          {total > 0 && <span className="file-size">{formatSize(total)}</span>}
        </div>
        <div className="upload-progress">
          <div style={{ width: percentTxt }} />
        </div>
        <div className="btns">
          {/* <a>
            {IconPause}
            <span>Pause</span>
          </a> */}
          <a onClick={onCancel}>
            {IconDelete}
            <span>{translate('creator.myVideos.30')}</span>
          </a>
        </div>
      </div>
    </div>
  );
};

type UploaderListState = ReturnType<typeof useUploaderList>;

const VideoUploaderAndList: FC<{ current?: Uploader; state: UploaderListState; onSelect: (it: Uploader) => void }> = ({ current, state, onSelect }) => {
  const { list, startUpload, remove } = state;
  const { translate } = useTranslator();
  return (
    <div className="video-uploader">
      {list.length <= 0 ? (
        <Dragger name="file" accept=".mp4" multiple={true} beforeUpload={startUpload} itemRender={() => <div />}>
          <div className="new-uploader">
            <div className="icon-wrap">{IconUpload}</div>
            <div className="title">{translate('creator.myVideos.22')}</div>
            <div className="desc">{translate('creator.myVideos.23')}</div>
            <Button type="primary">{translate('creator.myVideos.24')}</Button>
          </div>
        </Dragger>
      ) : (
        <div className="upload-list">
          <div className="append-uploader">
            <Dragger name="file" accept=".mp4" multiple={true} beforeUpload={startUpload} itemRender={() => <div />}>
              {IconAdd}
              <span>{translate('creator.myVideos.25')}</span>
            </Dragger>
          </div>
          {list.map((uploader, i) => (
            <VideoUploadingItem uploader={uploader} key={i} className={current?.id === uploader.id ? 'active' : ''}
              onSelect={() => onSelect(uploader)}
              onCancel={() => remove(uploader)} />
          ))}
        </div>
      )}
    </div>
  );
};

function submitForm(val: any) {
  return new PublishVideoService(val)
    .send()
    .then(() => {
      showSuccess(translate('creator.myVideos.31'));
    })
    .catch((err) => {
      message.error(err);
      return Promise.reject(err);
    });
}

export function getLabels() {
  return new Ajax<LabelItem[]>('/creator/videoLabelList', {}).post();
}

export const VideoFormModal: FC<{ collectid?: string; onClose: () => void; onPublish: () => void; }> = ({ onClose, onPublish, collectid }) => {
  const uploaderListState = useUploaderList();
  const { translate } = useTranslator();

  const [curId, setCurId] = useState<string>();
  const [auth, setAuth] = useState<string>('price');
  const cur = uploaderListState.list.find(it => it.id === curId) ?? uploaderListState.list[0];
  const [formData, setFormData] = useState({} as Record<string, any>);
  const { loading, loadWithPromise: submit } = useRequest(submitForm, { autoLoad: false });
  const { data: labels } = useRequest(getLabels);

  const doSubmit = () => {
    if (!cur || cur.status !== 'success') {
      showError(translate('creator.myVideos.32'));
      return;
    }
    let values = JSON.parse(JSON.stringify(getValues()));

    if (auth == 'price') {
      delete values.isfree;
    } else if (auth == 'free') {
      values.isfree = 0;
      delete values.price;
      delete values.tryminute;
    } else {
      values.isfree = 1;
    }
    submit({
      ...values,
      alivideoid: cur.videoId,
      url: cur.url,
      collectid
    }).then(() => {
      const last = uploaderListState.list.length <= 1;
      uploaderListState.remove(cur);
      onPublish();
      if (last) {
        onClose();
      }
    });
  };
  const getValues = () => {
    if (!cur) return '';
    let result = formData[cur.id] ?? {};

    return result;
  }
  const field = (name: string, defaultValue = '') => [
    getValues()[name] ?? defaultValue,
    (val: any) => {
      if (!cur) return;
      setFormData({ ...formData, [cur.id]: { ...getValues(), [name]: val } });
    }
  ];
  const fTitle = field('title');
  const fDesc = field('abstract');
  const fPrice = field('price');
  const fLabel = field('labelid');
  const fThumb = field('thumb', '0');
  const fTryminute = field('tryminute', '');
  return (
    <Modal className="creator-video-upload-modal" width="72.25rem" title={collectid ? translate('creator.playlist.17') : translate('creator.myVideos.3')} visible={true} onCancel={onClose}>
      <div className="intro-area">
        <div className="intro-item">
          {IconVideoFormat}
          <div>
            <div className="title">{translate('creator.myVideos.16')}</div>
            <div className="desc gray">{translate('creator.myVideos.17')}</div>
          </div>
        </div>
        <div className="intro-item">
          {IconVideoScreen}
          <div>
            <div className="title">{translate('creator.myVideos.18')}</div>
            <div className="desc gray">{translate('creator.myVideos.19')}</div>
          </div>
        </div>
        <div className="intro-item">
          {IconVideoFiles}
          <div>
            <div className="title">{translate('creator.myVideos.20')}</div>
            <div className="desc gray">{translate('creator.myVideos.21')}</div>
          </div>
        </div>
      </div>
      <VideoUploaderAndList state={uploaderListState} current={cur} onSelect={(it) => setCurId(it.id)} />
      {cur && (<>
        <div className="c-form">
          <div className="c-form-left">
            <h1>{translate('creator.myVideos.33')}</h1>
            <div className="desc">{translate('creator.myVideos.34')}</div>
            <div className="input-field">
              <label>{translate('creator.myVideos.35')} ({translate('creator.myVideos.36')})</label>
              <input value={fTitle[0]} onChange={e => fTitle[1](e.target.value)}  maxLength={60} />
            </div>
            {!collectid && <div className="input-field labels">
              <label>{translate('creator.myVideos.8')} </label>
              <Select mode="multiple" showArrow={true} onChange={(v) => {
                fLabel[1](v.join(','));
              }}>
                {(labels ?? []).map((l: any) => <Select.Option key={l.id} value={l.id}>{mainStore.isEn?l.enname: l.name}</Select.Option>)}
              </Select>
            </div>}
            <div className="input-field abstract">
              <label>{translate('creator.myVideos.37')}</label>
              <textarea value={fDesc[0]} onChange={e => fDesc[1](e.target.value)} />
            </div>
          </div>
          <div className="c-form-right">
            <h1>{translate('creator.myVideos.38')}</h1>
            <div className="desc">{translate('creator.myVideos.39')}</div>
            <div className="input-field covers">
              <label>{translate('creator.myVideos.40')} ({translate('creator.myVideos.36')})</label>
              <ImageUploader className="thumb-uploader" width={240} height={135}
                value={fThumb[0]}
                onChange={fThumb[1]}
                displayScale={.5}
                displayContent={(
                  <div className="disp">
                    {IconAdd2}
                    <span>{translate('creator.myVideos.41')}</span>
                  </div>
                )} />
            </div>
            {!collectid && <div className="auth-content">
              <h1>{translate('creator.myVideos.51')}</h1>
              <div className="desc">
                <Radio.Group defaultValue="price" onChange={(v) => {
                  setAuth(v.target.value);
                }} buttonStyle="solid">
                  <Radio.Button value="price">{translate('creator.myVideos.11')}</Radio.Button>
                  <Radio.Button value="free">{translate('creator.myVideos.10')}</Radio.Button>
                  <Radio.Button value="member">{translate('creator.myVideos.52')}</Radio.Button>
                </Radio.Group>
              </div>
            </div>}

            {!collectid && auth != 'free' && <div className="price-info">
              <div className="left-content">
                <div className="input-field2">
                  <label>{translate('creator.myVideos.44')}</label>
                  <input value={fPrice[0]} onChange={e => fPrice[1](e.target.value)} />
                </div>
              </div>
              <div className="right-content">
                <div className="input-field2">
                  <label>{translate('creator.myVideos.46')}</label>
                  <input value={fTryminute[0]} onChange={e => {
                    let inputValue: any = e.target.value;
                    const reg = /^-?\d*(\d*)?$/;
                    inputValue = inputValue.indexOf('0')==0?inputValue.slice(1):inputValue;
                      console.log("inputValue", inputValue)
                      if (((!isNaN(inputValue) && reg.test(inputValue))) || inputValue === '' || inputValue === '-') {
                      fTryminute[1](inputValue);
                    } else {
                      if (inputValue.length > 1) {
                        inputValue = inputValue.slice(0, inputValue.length - 1);
                      }
                      fTryminute[1](inputValue);
                    }
                  }} />
                </div>
              </div>

            </div>}
          </div>
        </div>
        <div className="btn-bar">
          <Button onClick={doSubmit} loading={loading}>{translate('creator.myVideos.45')}</Button>
        </div>
      </>)}
    </Modal>
  );
}