import { useEffect, useState } from "react";
import { useParams } from "react-router";
import { Avatar } from "../../components/Image";
import { Loading } from "../../components/Loading";
import { CreatorSubscribeService, CreatorUserService } from "../../services/CreatorService";
import { useRequest } from "../../utils/hooks";
import { Input } from 'antd';

import './UserProfile.scss';
import { translate } from "../../components/Text";
import { Icon_search_black } from "../../images/index/blackSearch";
import { NavLink } from "react-router-dom";
import eventBus from "../../utils/eventBus";
import { observer } from "mobx-react";
import { LocalStorageService } from "../../utils/storage";
import GlobalConfig from "../../config/GlobalConfig";
import { CreatorLoader } from "../Skelleton/creator";

const defaultBg = 'https://aolinews.oss-accelerate.aliyuncs.com/images/creatorBg.png'
const getUserData = ({ id = "" }) => new CreatorUserService(id).send();
const followUserRequest = ({ id = "", status = 1 }) => new CreatorSubscribeService({ objectid: id, objecttype: 1, status: status }).send();

export const UserProfile = observer(() => {
  const { id } = useParams<any>();
  const [refresh, setRefresh] = useState(false);
  const { data: user, load, loading } = useRequest(getUserData, { autoLoad: false });
  const { load: followUser, loading: following } = useRequest(followUserRequest, { autoLoad: false });
  useEffect(() => { load({ id }) }, [id]);
  if (!user || loading) {
    return (
        <CreatorLoader/>
    );
  }
  const navs = [
    [translate('creator.common.1'), '/videoList'],
    [translate('creator.common.2'), '/collList'],
  ];
  let searchkey = '';
  return (
    <div className="userContent">
      <img className="bg" src={user.background || defaultBg} alt="" />
      <div className="user-center-profile">
        <div className="user-item">
          <Avatar src={user.avatar} />
          <div className="info">
            <div className="name">{user.username}</div>
            <div className="user-desc">{user.profile}</div>

            <div className="user-nav">
              {navs.map(([txt, url]) => (
                <NavLink key={txt} className="user-nav-item" activeClassName="active" to={`/main/userCenter/${id}${url}`}>{txt}</NavLink>
              ))}
            </div>
          </div>
        </div>
        <div className="rightContent">
          <div className="follow" onClick={() => {
            if (following) {
              return;
            }
            let isLogin = LocalStorageService.get("_userInfo");
            if (!isLogin) {
              GlobalConfig.history.push('/login')
            }

            let status = user.isfollow == 1 ? 0 : 1;
            followUser({ id: user.id, status });
            user.isfollow = status;
            setRefresh(true);
          }}>{translate(`creator.author.${user.isfollow == 1 ? 2 : 1}`)}</div>
          <div className='searchWrapper'>
            <Input placeholder={translate('common.o')} onChange={(v) => {
              searchkey = v.target.value;
            }} onPressEnter={() => {
              eventBus.emit('userSearch', searchkey);
            }} />
            <div className='search' onClick={() => {
              eventBus.emit('userSearch', searchkey);
            }}>
              <img className='icon' src={Icon_search_black} alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});



