
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import eventBus from '../../utils/eventBus';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import './Creator.scss';
import { News_Bg_Img } from '../../images/NewsBg';
import { Sub_Bg_Img } from '../../images/SubBg';
import { Sub_ListBg_Img } from '../../images/SubListBg';
import { creatorStore } from '../../stores/CreatorStore';
import { useHistory } from 'react-router';

export const CreatorPage = observer(() => {
  useEffect(() => {
    creatorStore.getData();
  }, [])
  let history = useHistory();


  eventBus.addListener('toBottom', () => {
    creatorStore.getData(true);
  })
  return (
 <div className="CreatorPage">
      <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-50px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title">OLI </div>
          {/* <div className="subTitle">.</div> */}

        </div>
      </header>

      <div className="contentWrapper" style={{ backgroundImage: `url('${Sub_ListBg_Img}')` }}>
        <div className="newsContent">
          {creatorStore.loading ? <div className="loading">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
          </div> : creatorStore.newsList.map((n, i) => <div key={i} className="item" onClick={()=>{
            creatorStore.detail = n;
            window.scrollTo(0,0);
            history.push('/creatorNewsDetail')
          }}>
            <div className="content">
              <div className="title">{n.title}.</div>
              <div className="abstract">
                {n.abstract}
              </div>
              <div className="bottom">
                <div className="author">{n.author}</div>
                <div className="date">{n.createtime}</div>
              </div>
            </div>
            <div className="right">
              <img src={n.thumb} alt="" />
            </div>
          </div>)}

          {creatorStore.loadingMore && <div className="loadmore">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
          </div>}
        </div>
      </div>
    </div>
  );
}
)